module.exports = function (bh) {
    bh.match('alphabet-sort-item', function (ctx, json) {
        ctx.content([{
            elem: 'heading',
            tag: 'h2',
            content: [{
                tag: 'a',
                attrs: {
                    href: '#'
                },
                content: json.heading
            }]
        }, {
            elem: 'list',
            tag: 'ul',
            content: json.list.map(function (item) {
                return {
                    tag: 'li',
                    content: [{
                        tag: 'a',
                        attrs: {
                            href: '#'
                        },
                        content: item
                    }]
                }
            })
        }]);
    });
}