module.exports = function (bh) {
    bh.match('location__link', function (ctx, json) {
        ctx.mods({
            selected: json.selected
        });
        ctx.tag('a');
        ctx.attr('href', '#');
        ctx.content([json.icon ? {
            tag: 'img',
            attrs: {
                src: json.icon,
                alt: 'icon'
            }
        } : false, json.text], true);
    });
}