module.exports = function (bh) {
    let alphabet = ['А', 'Б', 'В', 'Г', 'Д', 'Е', 'Ж', 'З', 'И', 'К', 'Л', 'М', 'Н', 'О', 'П', 'Р', 'С', 'Т', 'У', 'Ф', 'Х', 'Ц', 'Ч', 'Ш', 'Э', 'Ю', 'Я', 'A-Z', '0-9'];
    bh.match('alphabet-list__letters', function (ctx, json) {
        ctx.tag('ul');
        ctx.content(alphabet.map(function (item) {
            return {
                tag: 'li',
                content: [{
                    tag: 'a',
                    attrs: {
                        href :'#'
                    },
                    content: item
                }]
            }
        }));
    });
}