module.exports = function (bh) {
    bh.match('profile__link', function (ctx, json) {
        ctx.tag('a');
        ctx.attr('href', '#');
        ctx.content([{
            tag: 'img',
            attrs: {
                src: json.icon,
                alt: 'icon'
            }
        }, json.text], true);
    });
}