module.exports = function (bh) {
    bh.match('catalog-menu-item', function(ctx, json) {
        ctx.content([{
            elem: 'heading',
            tag: 'h2',
            content: json.heading
        }, json.list ? {
            elem: 'list',
            tag: 'ul',
            content: json.list.map(function (item) {
                return {
                    tag: 'li',
                    content: [{
                        tag: 'a',
                        attrs: {
                            href: '#'
                        },
                        content: item
                    }]
                }
            })
        } : false], true);
    });
}

