module.exports = function (bh) {
    bh.match('article-item', function (ctx, json) {
        ctx.tag('article');
        ctx.content([json.image ? {
            elem: 'image',
            content: [{
                tag: 'img',
                attrs: {
                    src: json.image,
                    alt: json.heading
                }
            }]
        } : false, {
            elem: 'column',
            content: [{
                elem: 'heading',
                tag: 'h3',
                content: [{
                    tag: 'a',
                    attrs: {
                        href: '#'
                    },
                    content: json.heading
                }]
            }, {
                elem: 'text',
                tag: 'p',
                content: json.text
            }, json.textSecond ? {
                elem: 'text',
                tag: 'p',
                content: json.textSecond
            } : false,
                json.more ? {
                elem: 'more',
                tag: 'a',
                attrs: {
                    href: '#' + json.moreId,
                    'data-toggle': 'collapse',
                    role: 'button',
                    'aria-expanded': 'false',
                    'aria-controls': json.moreId
                    
                },
                content: json.more
            } : false, json.more ? {
                    elem: 'text',
                    mix: {
                        block: 'collapse'
                    },
                    tag: 'p',
                    attrs: {
                        id: json.moreId
                    },
                    content: 'Etiam aliquam sem ac velit feugiat elementum. Nunc eu elit velit, nec vestibulum nibh. Curabitur ultrices, diam non ullamcorper blandit, nunc lacus ornare nisi, egestas rutrum magna est id nunc. Pellentesque imperdiet malesuada quam, et rhoncus eros auctor eu. Nullam vehicula metus ac lacus rutrum nec fermentum urna congue. Vestibulum et risus at mi ultricies sagittis quis nec ligula. Suspendisse dignissim dignissim luctus. Duis ac dictum nibh. Etiam id massa magna. Morbi molestie posuere posuere. '
                } : false]
        }])
    });
}