module.exports = function (bh) {
    bh.match('nav-item', function(ctx, json) {
        if (json.dropdown) {
            ctx.mix({ block: 'dropdown' });
        }
        ctx.tag('li');
        ctx.content({
            block: 'nav-link',
            tag: 'a',
            attrs: {
                href: json.dropdown ? 'javascript:void(0);' : '#',
                'drop-menu': json.dropdown ? json.menu : false
            },
            mix: json.dropdown ? {
                block: 'dropdown-toggle'
            } : false,
            content: json.link
        }, true);
    });
}

