module.exports = function (bh) {
    bh.match('catalog-item', function (ctx, json) {
        ctx.content([{
            block: 'favorites'
        }, {
            elem: 'link',
            tag: 'a',
            attrs: {
                href: '#'
            },
            content: [{
                elem: 'name',
                tag: 'span',
                content: json.name
            }, {
                elem: 'manufacturer',
                tag: 'span',
                content: json.manufacturer
            }, {
                elem: 'image',
                tag: 'span',
                content: [{
                    tag: 'img',
                    attrs: {
                        src: json.image,
                        alt: 'image'
                    }
                }]
            }]
        }, {
            elem: 'properties',
            content: [json.substance ? {
                elem: 'substance',
                content: [{
                    tag: 'span',
                    content: 'Действующее вещество:'
                }, {
                    tag: 'a',
                    attrs: {
                        href: '#'
                    },
                    content: json.substance
                }]
            } : false, {
                elem: 'shelf-life',
                tag: 'span',
                content: ['Срок годности: до ' + json.shelfLife]
            }]
        }, {
            elem: 'bottom',
            tag: 'form',
            content: [{
                block: 'price',
                tag: 'span',
                content: [{
                    elem: 'action',
                    tag: 'span',
                    content: json.action
                }, json.price]
            }, {
                block: 'counter',
                content: [{
                    elem: 'minus',
                    tag: 'button',
                    attrs: {
                      type: 'button'
                    },
                    mix: {
                        block: 'button-circle'
                    },
                    content: [{
                        block: 'iconminus',
                        tag: 'i'
                    }]
                }, {
                    block: 'input',
                    attrs: {
                        type: 'text',
                        value: '1'
                    }
                }, {
                    elem :'plus',
                    tag: 'button',
                    attrs: {
                        type: 'button'
                    },
                    mix: {
                        block: 'button-circle'
                    },
                    content: [{
                        block: 'iconplus',
                        tag: 'i'
                    }]
                }]
            }, {
                block: 'button',
                content: 'КУПИТЬ'
            }]
        }], true);
    });
}