module.exports = function (bh) {
    bh.match('phone__number', function (ctx, json) {
        ctx.tag('a');
        ctx.attrs({
            href: 'tel:' + json.number
        });
        ctx.content([json.number, json.subtitle ? {
            tag: 'small',
            content: json.subtitle
        }: false], true);
    });
}